@import url('https://fonts.googleapis.com/css?family=Suranna|Work+Sans:400,400i');
html,
body {
    background: #F5F5F5;
    font-family: 'Work Sans', -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif !important;
}

.wordCard__content {}

.wordCard__word {
    font-family: 'Work Sans', serif;
    font-weight: 400;
    font-size: 32px;
}

.wordCard__sentence {
    font-family: 'Work Sans', serif;
    font-weight: 400;
    font-style: italic;
}

.fa {
    font-size: 1.4em;
    margin: 0px 4px;
    width: 18px;
}

.hover {
    cursor: pointer;
}

.word_input {
    border: 0;
    -webkit-box-shadow: none;
            box-shadow: none;
    border-bottom: 2px solid rgb(180, 180, 255);
    border-radius: 0;
    outline: none;
    caret-color: blue;
}

.bordered {
    border: 2px solid black;
}

.word_input:focus {
    -webkit-box-shadow: none;
            box-shadow: none;
}


